@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;1,300;1,400&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  position: relative;
  width: 100%;
  min-height: 100vh;
  padding: 20px;
  background: linear-gradient(#00000044, #00000044),
    url(/14.WeatherApp/static/media/clouds_1280.4930c5b2.jpg);
  background-size: cover;
  background-position: center;
  background-color: #00000055;
  font-family: "Montserrat", Arial, "Helvetica Neue", Helvetica, sans-serif;
}

.App .wrapper {
  max-width: 1024px;
  margin: 0 auto;
}

.App .title {
  color: #1a486f;
  padding: 10px;
  margin-bottom: 10px;
}

.search-bar {
  width: 80%;
  max-width: 600px;
  padding: 0.8em 1.4em;
  margin: 0 0 10px;
  border: none;
  border-radius: 6px;
  color: white;
  background-color: #000000cc;
  font-family: "Montserrat", Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-size: 1.2em;
}

input:-webkit-autofill {
  border: none;
  -webkit-text-fill-color: white;
  -webkit-box-shadow: 0 0 0px 1000px #000000dd inset;
  -webkit-transition: background-color 1s ease-in-out 0s;
  transition: background-color 1s ease-in-out 0s;
}

input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
  border: none;
  -webkit-text-fill-color: white;
  -webkit-box-shadow: 0 0 0px 1000px #000000ee inset;
}

.search-bar:focus-visible {
  outline: none;
  background-color: #000000ee;
}

@media (max-width: 640px) {
  .search-bar {
    margin: 0 0 10px;
    width: 100%;
  }
}

.info {
  padding: 30px;
  margin: 10px 0;
  border-radius: 10px;
  /* width: fit-content; */
  width: 100%;
  max-width: 800px;
  color: white;
  background-color: #000000aa;
}

.info .city {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0px 30px 10px 10px;
  margin-bottom: 10px;
  font-size: 1.4em;
  font-weight: 400;
  color: white;
}
.info .img {
  max-width: 50px;
  max-height: 50px;
  margin-right: 0.4em;
}
.info span {
  text-transform: capitalize;
}

.info .city span {
  margin-left: 0.3em;
}

.info h4 {
  margin-left: 10px;
  padding: 10px 0;
  color: white;
  font-weight: 400;
}

@media (max-width: 640px) {
  .info {
    margin: 10px 0;
    width: 100%;
  }
}

